.App {
    text-align: center;
    font-family: 'Sen';
    font-size: 1.2rem;
}

#welcome{
    background-color: #FFFFFF; 
    text-align: center;
    padding-bottom: 40px; 
    margin: auto; 
    width: 100%;
    text-align: justify;
  

}
* {
    box-sizing: border-box;
  }
  
.App-link {
    color: #61dafb;
}

.square {
    font-family: 'Montserrat', sans-serif;
    display: inline-block;
    width: 100%;
    padding-bottom: 30%;
    margin: 1.66%;
    background-color: #ffffff;
    color: #1E1E1E;
    text-align: justify;
    overflow-y: scroll;
    height: 350px;
    border: 1px solid #1E1E1E;
}



.details{
    display: block;
    list-style-type: disc;
    margin-block-start: 0em;
    margin-block-end: 0em;
    margin-inline-start: 0px;
    margin-inline-end: 0px;
    padding-inline-start: 0px;
}






/* width */
::-webkit-scrollbar {
    background-color: orange;
    width: 3px;
  }
  
  /* Track */
  ::-webkit-scrollbar-track {
    background: #f1f1f1;
  }
  
  /* Handle */
  ::-webkit-scrollbar-thumb {
    background: hotpink;
  }
  
  /* Handle on hover */
  ::-webkit-scrollbar-thumb:hover {
    background: #2eb8b8;
  }