.fade-appear{
    opacity: 0;
    z-index: 1;
}

.fade-appear.fade-appear-active{
    opacity:1;
    transition: opacity 500ms linear;
}

.fade-enter{
    opacity: 0;
    z-index: 1;
}
.fade-enter.fade-enter-active{
    opacity:1;
    transition: opacity 500ms linear;
}

.text-to-render{
   font-family: 'Lobster';
   font-size: 6rem;  
   color: hotpink;
}

#wilkommen{
    background-color: transparent;
    font-family: 'Sen';
    height: 50px;   
}

.transition-text{
    color: hotpink;
    background-color: transparent;
}

.envolvente{
    background-color: transparent;
    height: 500px;
    width: 1000px;
    position: fixed;
    top: 50%;
    left: 50%;
    margin-top: -250px;
    margin-left: -500px;
}

#show-me-the-button{
    height: 50px;  
    background-color: purple;
}

#redirect-not-aut{
    margin-top: 1%;
    background-color: transparent;
    height: 100px; 
    font-family: 'Sen';  
}

.enter{ 
    background-color: transparent; 
    color: hotpink;
    height: 50px;
    font-family: 'Sen';
    font-size: 1.2rem;
    padding: 0;
    cursor: pointer;
}

#text-redir{
    background-color: transparent;
    height: 50px;
    line-height: 50px;
    font-family: 'Sen';
}


