.contentor-de-fora{
    height: 40vh;
    width: 60vh;
    background-color: transparent;
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}
.contentor-de-fora::-webkit-scrollbar {
    display: none;
}

.contentor-de-fora {
    -ms-overflow-style: none;
}

.dd-wrapper{ 
    flex-wrap: wrap; 
    border: 1px solid black;
    overflow: auto;   
    background-color: transparent;
    width: 100%; 
}

.dd-header{
    justify-content: space-between;
    cursor: pointer;
    width: 100%; 
    padding: 0 20px;
    border: 1px solid black;
    background-color: transparent;
    overflow-y: scroll; /* added*/
}

.dd-list {
    padding: 0;
    margin: 0;
    width: 100%;
    margin-top: 0;
    background-color: transparent;
    overflow-y: scroll; 
    max-height:30vh;
    border: 1px solid black;
}

li {
    list-style-type: none;
    background-color: transparent;
    overflow-y: scroll; 
}

#list-api {
    display: flex;
    justify-content: space-between;
    background-color: white;
    font-size: 16px;
    padding: 15px 20px 15px 20px;
    border: 0;
    border-bottom: 1px solid #ccc;
    width: 100%;
    text-align: left;
    background-color: transparent;
    overflow-y: scroll; 
    cursor: pointer;
}

.dd-header-title-bold{
    font-weight: bold;
}
.info-selected{
    width: 100%;
    background-color: transparent;
    height: 80px; 
    margin-top: 15px; 
    display: flex;
    justify-content: center;
    align-items: center;
}

.button-container{
    background-color: transparent;
    height: 55px;
    align-items: center;
    justify-content: center;
    width: 100%;
}

.send{
    background-color: white;
    transform :translateY(40%);
    border: 1px solid black;
    margin-right: 2px;
}

.text-results{  
    padding:0px;
    background-color: white;
    text-align:justify;
}

.flatlist{
    overflow-y: scroll; 
    background-color: transparent;
    max-height: 20vh;
   
    width: 100%;      
    background-color: white;
    margin-top: 2vh;
    padding-top: 0.4vh;   
}

.chosen-one{    
    background-color: transparent;
    height: 80px;   
}

.render-msg{
    transform :translateY(80%);
}

.experimental{
    height: 38px;
    margin-top:16px; 
    background-color: transparent;
}
.out{
    position: fixed;
    top: 50%;
    left: 50%;
    height: 400px;
    width: 400px; 
    margin-top: -200px; 
    margin-left: -200px;
    background-color: transparent;
    justify-content: center;
    align-items: center;
    display: block;
    margin: auto;
}


